



























































































import {
	mapState,
	mapActions
} from "vuex";
import {
	API_DOMAIN
} from '../../../utils/config.js';
export default {
	computed: {
		...mapState(["token"]),	
	},
	data() {
		return {
			shareParams: {
				provider: "weixin",
				type: 0,
				href: "http://api.rongxiaoxiao.com/about_client",
				title: "融小小分享",
				summary: "融小小分享内容",
				imageUrl: "https://m-fat.wolaidai.com/web-static/webapp/auto/img/home_banner_new_user.png",
			},
			isShowMask: false,
			logoSrc: '../../../static/img/logo.png' // /static/img/info.png  
		}
	},
	onShareAppMessage() {
		return {
			title: this.title,
			path: '/pages/home/home',
			imageUrl: this.imageUrl
		}
	},		
	methods: {
		showMask(flag) {
			this.isShowMask = flag;
		},
		goDetail(path) {
			uni.navigateTo({
				url: `/pages/${path}`
			})
		},
		shareWeiXin(scene) {
			var params = this.shareParams;
			uni.share({
				scene,
				...params,
				success: function(res) {
					//plus.nativeUI.toast('分享成功');
				},
				fail: function(err) {
					plus.nativeUI.toast('分享失败');
				}
			});
		},
		shareQQ() {
			var params = this.shareParams;
			uni.share({
				...params,
				provider: 'qq',
				success: function(res) {
					//plus.nativeUI.toast('分享成功');
				},
				fail: function(err) {
					plus.nativeUI.toast('分享失败');
				}
			});
		},
		shareQQ2() {
			var shareObj = null;
			plus.share.getServices(
				//获取分享服务成功
				function(services) {
					for (var i in services) {
						//判断微信分享服务
						if (services[i].id == 'qq') {
							shareObj = services[i];
						}
					}
					if (shareObj == null) {
						plus.nativeUI.toast('获取分享服务失败');
						return;
					}else{
						plus.nativeUI.toast(JSON.stringify(services));
					}

					//分享核心代码
					shareObj.send({
							content: "我是QQ分享",
							thumbs: ['../../static/img/blogo.png'],
							href: 'https://www.baidu.com/',
							title: 'test'
						},
						function() {
							plus.nativeUI.alert("分享成功！");
						},
						function(e) {
							plus.nativeUI.alert("分享失败：" + e.message);
						}
					);
				},
				//获取分享服务失败
				function(e) {
					plus.nativeUI.alert(e.message);
				}
			);

		},
		updateUserinfo(){
			var params = {
				AVATAR: this.logoSrc
			};
			this.API.updateUserinfo(params).then(res => {
				this.Util.Toast.success("头像上传成功");				
			}).catch(res => {
				
			})
		},
		upload() {
			uni.chooseImage({
				count: 1,
				success: (chooseImageRes) => {
					this.Util.Toast.showLoading("上传中...")
					const tempFilePaths = chooseImageRes.tempFilePaths;
					const uploadTask = uni.uploadFile({
						url: `${API_DOMAIN}/uploadfile`,
						filePath: tempFilePaths[0],
						name: 'file',
						formData: {
							'token': uni.getStorageSync('token')
						},
						success: (uploadFileRes) => {
							this.logoSrc = JSON.parse(uploadFileRes.data).data;
							uni.setStorageSync('avatar', this.logoSrc);
							this.updateUserinfo();
							
						},
						fail: () => {
							this.Util.Toast.hideLoading();
							this.Util.Toast.toast("上传失败");
							console.log('上传失败')
						}
					});

					uploadTask.onProgressUpdate((res) => {
						if (res.progress == 100) {
							this.Util.Toast.hideLoading();
							this.Util.Toast.toast("上传成功");
						}
					});
				}
			})
		}
	},
	onLoad() {
		
	},
	onShow() {
		if (!this.token) {
			uni.showToast({
				title: '请先登录',
				icon: 'none',
				duration: 1500,
				mask: true
			});
			setTimeout(()=>{
				uni.redirectTo({
					url: '/pages/login/login'
				})
			}, 2000	)	
			return
		}
		var avatar = uni.getStorageSync('avatar');
		if(avatar){
			this.logoSrc = avatar;
		}
	}

}
